import React from 'react';
import {Button, ButtonProps} from '@mui/material';

interface ButtonType extends ButtonProps {
    childern:React.ReactNode;
    variant?: 'contained'| "outlined";
    size?: 'medium'|"large";
    style?: React.CSSProperties;
    onClick?: () => void;
    startIcon?: React.ReactNode;
    disabled?: boolean;

}

const ButtonComponent:React.FC<ButtonType> = ({childern,variant,size,style,onClick,startIcon,disabled}:ButtonType) => {
  return (
    <Button  variant={variant} size={size} sx={style} onClick={onClick} startIcon={startIcon} disabled={disabled} ></Button>
  )
}

export default ButtonComponent;